.mainfooter {
  background-color: #f5f3e6;
  background-size: cover;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  height: 100%;
}

h2,
.ulfooter,
.linkredes {
  width: 33%;
  padding: 4rem;
}

.lifooter {
  list-style: none;
}

.lifooter02 {
  list-style: none;
  text-decoration: none;
}

.lifooter02 a {
  text-decoration: none;
}

@media screen and (min-width: 400px) and (max-width:551px){
  .mainfooter {
    overflow: hidden;
  }

  .mainfooter {
    background-color: #f5f3e6;
    background-size: cover;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    height: 100%;
    margin-left: -8rem;
    
  }
  
  
}
@media screen and (min-width: 551px) and (max-width:720px){
  .mainfooter {
    overflow: hidden;
  }

  .mainfooter {
    background-color: #f5f3e6;
    background-size: cover;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    height: 100%;
    margin-left: -8rem;
    
  }
}