.header {
  background-color: #faf8f0;
  background-size: cover;
  margin: 0;
  padding: 0;
}

.divIconHeader {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  margin: 0 10rem;
}

.ulHeader {
  display: flex;
  flex-direction: row;
  justify-content: center;
  list-style: none;
}

.liHeader {
  padding: 0 1rem;
  font-size: 1rem;
}

.iconHeader1,
.iconHeader2 {
  background-color: transparent;
  border: none;
  cursor: pointer;
  color: #000;
  text-decoration: none;
}

.iconHeader1 {
  transform: rotate(180deg);
  -webkit-transform: rotate(180deg);
  -moz-transform: rotate(180deg);
  -ms-transform: rotate(180deg);
  -o-transform: rotate(180deg);
}

.linkh1Header {
  text-decoration: none;
  color: #000;
}

.h1Header {
  text-align: center;
  padding-top: 4rem;
  text-decoration: none;
}

.fab {
  padding-left: 1rem;
  color: #000;
}

@media screen and (min-width: 400px) and (max-width: 550px){
  .header {
    overflow: hidden;
  }
  .ulHeader {
    display: flex;
    flex-direction: row;
    justify-content: center;
    list-style: none;
    margin-left: -25px;
  }
  .divIconHeader {
    margin: 0 1rem;
  }
  .fab, .fas{
    font-size: 25px;
    margin: 0 1rem;

  }
  .liHeader {
    padding: 0.5rem;
    font-size: 1rem;
    margin: 0 0.5rem;
    margin-left: 2rem;
  }  

  .h1Header {
    font-size: 20px;
  }
  .iconHeader2{
    margin: 0 1rem;
  }
  
}
@media screen and (min-width:400px) and(max-width: 551px){
  .header {
    overflow: hidden;
  }

  .divIconHeader {
    margin: 0 1rem;
  }
  .fab, .fas{
    font-size: 25px;
    margin: 0 1rem;
  }
  .liHeader {
    padding: 0.5rem;
    font-size: 1rem;
    margin: 1 0.5rem;
    margin-left: 1rem;
  } 

  .h1Header {
    font-size: 20px;
  }
  .iconHeader2{
    margin: 0 1rem;
  }
}
@media screen and (min-width: 551px) and (max-width:720px){  
  .header {
    overflow: hidden;
  }

  .divIconHeader {
    margin: 0 4rem;
    margin-left: 12px;
  }
  .fab, .fas{
    font-size: 25px;
    margin: 0 1rem;
  }
  .liHeader {
    padding: 0.5rem;
    font-size: 1rem;
    margin: 1 0.5rem;
    margin-left: 2rem;
  }
  .ulHeader{
    margin-left: -6rem;

  } 

  .h1Header {
    font-size: 20px;
  }
  .iconHeader2{
    margin: 0 1rem;
  }
}