.mainhome {
  background-color: #faf8f0;
  background-size: cover;  
}

.divScreenshot01{
  display: flex;
  justify-content: center; 
  
}

.Screenshot01{
  width: 80%;
  margin: 2%;  
}

.divScreenshot02{
  display: flex;
  justify-content: left;  
}

.Screenshot02{
  width: 50%;
  margin-left: 10%;  
}

.divScreenshot03{
  display: flex;
  justify-content: center; 
  
}

.Screenshot03{
  width: 80%;
  margin: 2%;  
}

@media (min-width: 400px) and (max-width: 551px) {  

  .mainhome { 
    overflow: hidden;
  }
  
  .Screenshot01{
    width: 80%;
    margin: 2%;
    margin-left: -3rem; 
  }
  .Screenshot02{
    width: 80%;
    margin-left: 12px; 
  }
    
  .Screenshot03{
    width: 80%;
    margin: 2%;
    margin-left: -3rem;  
  }
  
}
@media (min-width: 551px) and (max-width:720px){  

  .mainhome { 
    overflow: hidden;
  }
  
  .Screenshot01{
    width: 80%;
    margin: 2%;
    margin-left: -5rem; 
  }
  .Screenshot02{
    width: 80%;
    margin-left: 12px; 
  }
    
  .Screenshot03{
    width: 80%;
    margin: 2%;
    margin-left: -5rem;  
  }
  
}